.home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0.1rem;
}

/* Headings */
h2, h3, h5, h1 {
  color: rgb(255, 255, 255);
  font-family: "Poppins, sans-serif";
  font-weight: 500;
  text-align: center;
  margin: auto;
}

/* Font Sizes */
h2, h3, h5, h1 {
  font-size: max(2vw, 1rem); /* Adjust as needed */
}

/* Icons */
content {
  color: rgb(5, 176, 219);
  font-size: 1rem;
  font-weight: 400;
  text-align: justify;

}

/* Logo and Gif */
.logo, gif {
  max-width: 100%; /* Ensure the image doesn't exceed its container width */
  height: auto; /* Maintain the aspect ratio of the image */
}

/* Welcome Animation */
.welcome {
  text-align: center;
  font-size: 48px;
}

.welcome h2 {
  position: relative;
  display: inline-block;
  perspective: 500px;
}

.welcome h2 span {
  position: relative;
  display: inline-block;
  transform-origin: bottom;
  transform: rotateX(90deg);
  opacity: 0;
  animation: rotateText 4s infinite alternate cubic-bezier(0.25, 0.1, 0.25, 1);
  color: rgb(5, 176, 219); /* Change color here */
}

/* Animation Keyframes */
@keyframes rotateText {
  0% {
      transform: rotateX(90deg) translateY(50px);
      opacity: 0;
  }
  50% {
      transform: rotateX(0deg) translateY(0);
      opacity: 1;
  }
  100% {
      transform: rotateX(0deg) translateY(-50px);
      opacity: 0;
  }
}
