/* Resume.module.scss */
.resume {
    display: flex;
    flex-wrap: wrap;
    gap: 5rem;
    @media screen and (max-width: 768px) {
      gap: 4rem;
    }
  }

  .i{
    font-size: 0.9em; /* Set font size to 90% of the default size */
    color: var(--blue); /* Set the desired color */
  }

  .path,
  .skills {
    flex: 1;
  }
  
  .experience,
  .hobbies,
  .programmingSkills {
    margin-top: 1rem; /* Adjust margin as needed */
    color: var(--blue); /* Assuming you have defined --blue color variable */
  }
  
  @media screen and (max-width: 768px) {
    .experience,
    .hobbies,
    .programmingSkills {
      margin-top: 2rem; /* Adjust margin as needed for smaller screens */
    }
  }
  
  .spacing {
    margin-bottom: 20px; /* Adjust margin as needed */
  }